import React from "react";
import { Link } from "react-router-dom";
import bannerLogo from '../../assets/logo.png';
import '../../index.css';


function Nav(props){
    return(
    <nav id="main-nav">
        <div className="row">
            <div className="container">

                <div className="logo">
                    <Link to="/"><img src={bannerLogo} alt="logo" /></Link>
                </div>

                <div className="responsive"><i data-icon="m" className="ion-navicon-round"></i></div>

                <ul className="nav-menu list-unstyled">
                    <li><Link to="/" className="smoothScroll" >Home</Link></li>
                    <li><Link to="/about" className="smoothScroll">About</Link></li>
                    <li><Link to="/employerlist" className="smoothScroll">Employers</Link></li>
                    <li><Link to="/userlist" className="smoothScroll">All users</Link></li>
                    <li className="nav-item dropdown">
                        <Link to="#" className="smoothScroll dropdown-toggle" data-toggle="dropdown" role="button" aria-haspopup="true">Sign Up</Link>
                        <div className="dropdown-menu">
                            <Link to="/signup" className="smoothScroll dropdown-item">Job Seeker/Hobbyist</Link>
                            {/* <Link to="/signupemployer" className="smoothScroll dropdown-item">Employers</Link> */}
                        </div>
                    </li>
                    <li><Link to="/login" className="smoothScroll">Login</Link></li>
                    <li><Link to="/contact" className="smoothScroll">Contact Us</Link></li>
                </ul>
            </div>
        </div>
    </nav>
    );
}


export default Nav;