import React, {useEffect, useState, useContext} from "react";
import {gql, useQuery, useLazyQuery} from "@apollo/client";
import {AuthContext} from '../utils/authContext';
import { useHistory } from "react-router-dom";
import {Header} from '../components';
import homeimage from "../assets/images/employeeproduct.jpg";


const getAllPosts = gql`
            {
                allPosts {
                    id
                    title
                    description
                }
            }
        `;

const role = "H!red";

function Home() {
  
    const {data, loading, error} = useQuery(getAllPosts);

    const [fetchPosts, {data:posts}] = useLazyQuery(getAllPosts);

    const {state, dispatch} = useContext(AuthContext);

    //use history gives us the history object of the browser; We can grab URL params.

    let history = useHistory()

    const updateUserName = () =>{
        dispatch({
            type:'LOGGED_IN_USER', 
            payload: "Franco"});
    }

    console.log(history);


    if(loading){
    return(
        <p className="p-5">
            Loading....
        </p>
      )
    }


  return (
    <>
      <Header image={homeimage} role={role}/>
      <main id="main">
        <div className="container">
          <div className="row">
            {data.allPosts.map((post, i) =>{
              return(
              <div key={i} className="col-md-6">
                <div className="card">
                  <div className="card-body">
                    <div className="card-title">
                      <h4>{post.title}</h4>
                    </div>
                    <div>
                      <p className="card-text">{post.description}</p>
                      <hr/>
                    </div>
                  </div>
                </div>
              </div>
              )
            })}
          </div>
          <button type="button" onClick={()=>{fetchPosts()}} className="btn-btn-raised btn-primary">Fetch Post</button>
            <hr/>
            {JSON.stringify(posts)}
            {JSON.stringify(state.user)}
            <button className="btn btn-primary" onClick={updateUserName}>Change User Name</button>
        </div>
    </main>
    </>
  );
}

export default Home;
