import React, { useState } from "react";
import { Link } from "react-router-dom";
import {Header} from '../../components';
import homeimage from "../../assets/images/employeeproduct.jpg";

function Login() {
    const imageDisplayed = homeimage;
    const roleDisplayed = "Get H!red";
return (
    <main id="main">
        <Header image={imageDisplayed} role={roleDisplayed}></Header>
        <div id="contact" className="paddsection" style={{margin:0}}>
            <div className="container">
                <div className="contact-block1">
                    <div className="row">

                        <div className="col-lg-6 mycontent-left" style={{paddingRight:100}}>
                            <div className="contact-contact">

                                <h2 className="section-title text-center">User Login</h2>

                                <form className="php-email-form">
                                <div className="row">

                                    <div className="col-lg-6">
                                        <div className="form-group contact-block1">
                                            <input type="email" name="email" className="form-control" id="email" placeholder="email"/>
                                            <div className="validate"></div>
                                        </div>
                                    </div>

                                    <div className="col-lg-6">
                                        <div className="form-group">
                                            <input type="password" className="form-control" name="password" id="pwd" placeholder="Password"/>
                                            <div className="validate"></div>
                                        </div>
                                    </div>


                                    <div className="col-lg-12">
                                        <input type="submit" className="btn btn-defeault btn-send" value="Login" />
                                    </div>
                                </div>
                            </form>

                            </div>
                        </div>

                        

                        <div className="col-lg-6" style={{paddingLeft:100}}>
                            <div className="contact-contact">

                                <h2 className="section-title text-center">Employer Login</h2>

                                <form className="php-email-form">
                                <div className="row">

                                    <div className="col-lg-6">
                                        <div className="form-group contact-block1">
                                            <input type="email" name="email" className="form-control" id="email" placeholder="email" />
                                            <div className="validate"></div>
                                        </div>
                                    </div>

                                    <div className="col-lg-6">
                                        <div className="form-group">
                                            <input type="password" className="form-control" name="password" id="pwd" placeholder="Password" />
                                            <div className="validate"></div>
                                        </div>
                                    </div>


                                    <div className="col-lg-12">
                                        <input type="submit" className="btn btn-defeault btn-send" value="Login" />
                                    </div>
                                </div>
                            </form>

                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </main>
);
}


export default Login;